import React from 'react';
import PropTypes from 'prop-types';
import {Link} from "react-router-dom";
import './Home.css';

const categories = [
    {
        "name": "Slide Rules",
        "url": "/sliderules",
        "image": "https://upload.wikimedia.org/wikipedia/commons/b/be/Slide_rule_cursor.jpg"
    },
    {
        "name": "Home",
        "url": "/",
        "image": "https://loremflickr.com/200/300"
    },
    {
        "name": "VIM",
        "url": "/vim",
        "image": "https://upload.wikimedia.org/wikipedia/commons/thumb/9/9f/Vimlogo.svg/544px-Vimlogo.svg.png"
    },
    {
        "name": "Work",
        "url": "/work",
        "image": "https://loremflickr.com/200/300"
    },
    {
        "name": "HOI",
        "url": "/hoi",
        "image": "https://loremflickr.com/200/300"
    }
];

const Card = ({
    name,
    url,
    image
}) => (
    <div className="gridBox">
        <Link to={url}>
            <div
                className="gridContent"
                style={{
                    backgroundImage: `url(${image})`
                }}
            >
                {name}
            </div>
        </Link>
    </div>
);

Card.propTypes = {
    name: PropTypes.string,
    url: PropTypes.string,
    image: PropTypes.string
}

const Home = () => (
    <div className="homeContainer">
        <h2>Home!</h2>
        <div className="gridWrap">
            {
                categories.map((category) => (
                    <Card
                        key={category.name}
                        name={category.name}
                        url={category.url}
                        image={category.image}
                    />
                ))
            }
        </div>
    </div>
);
export default Home;
